}
.conversation {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  cursor: pointer;
  border-radius: 1rem;
}

.conversation:hover {
  background-color: rgb(202, 224, 247);
}

.conversationImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.conversationName {
  font-weight: 500;
}

.message {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 20px;
  margin-left: 10px;
}

.message-cover {
  display: -webkit-flex;
  display: flex;
}

.message-text {
  padding: 10px;
  border-radius: 20px;
  background-color: #66b9f0;
  max-width: 350px;
}

.message-meta {
  font-size: 12px;
  margin-top: 10px;
}

.message.own {
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.message.own .message-text {
  background-color: rgb(238, 238, 238);
  background-color: #aae7ff;
}

@media (min-width: 992px) {
  .toggle-wrapper {
    display: none;
  }
}
.chat-toggle {
  cursor: pointer;
}

.chat {
  /* max-width: 70vw; */
  /* min-height: 80vh; */
  margin: 1rem;
  background-color: white;
  border-radius: 1rem;
  border-style: solid;
  border-color: white;
  box-shadow: 0px 4px 1px rgba(0, 0, 0, 0.25);
  /*   display: flex; */
}

.sidebar {
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 1rem;
}

.chatbox {
  max-width: 50vw;
  -webkit-flex: 4 1;
          flex: 4 1;
  padding: 1rem;
}

.chatbox-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: relative;
}

.chat-window {
  height: 65vh;
  overflow-y: scroll;
  padding-right: 10px;
  background-color: rgb(248, 248, 248);
}

.chat-input {
  margin-top: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 1rem;
}

.chat-input-textarea {
  width: 85%;
  height: 70px;
  padding: 10px;
}

.chat-input-button {
  margin-top: 1.5rem;
  width: 70px;
  height: 40px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 1rem;
}

